import React from "react";
import Navigation from "./Navigation";
import {Link} from "react-router-dom";

export default class BlogActivity extends React.Component {
    render() {
        return(
            <div>
                <Navigation/>
                <div>
                    <div style={{marginTop: "100px"}}>
                        <div class="row">
                            <div class="col-12 col-sm-2 fix" style={{marginBottom: "20px"}}>
                                <h2 style={{marginBottom: "20px"}}></h2>
                                <h3>daan<sup>1</sup> seon<sup>4</sup></h3>
                                <div>simply; purely; merely</div>
                            </div>
                            <div class="col-12 col-sm-9">
                                <div>
                                    <h2>Happenings</h2>
                                    <div> emotion  is something I'm doing in my pasttime while mobile app ideas are pivoting. I'm using InDesign for song inspirations
                                    and seeing where the creations take themselves.</div>
                                </div>
                                <div>
                                    <h4>Future Happenings</h4>
                                    <div> os design  Recreating what I had to do for INFO 365 - Mobile App Design by defining my own OS.</div>
                                    <div> geometric  Making designs restricted by the number of sides or particular ratios.</div>
                                </div>
                                <hr/>
                                <div>
                                    <Link class="blog" to="/colorgreen">2018.06.02  emotion  new politics - color green</Link> <br/>
                                    <Link class="blog" to="/awas">2018.06.01  emotion  the cab - angel with a shotgun</Link> <br/>
                                    <Link class="blog" to="/iflid">2018.05.31  emotion  two feet - i feel like i'm drowning</Link> <br/>
                                    <Link class="blog" to="/dirtyneon">2018.05.31  emotion  3lau - dirty neon</Link> <br/>
                                    <Link class="blog" to="/backbone">2018.05.29  emotion  daughtry - backbone</Link> <br/>
                                    <Link class="blog" to="/genesis">2018.05.22  emotion  jj lin - genesis</Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}


